import Vue from 'vue';
import Vuex from 'vuex';
import clone from '@/lib/clone';
import createId from '@/lib/createId';
import router from '@/router';

Vue.use(Vuex); // storevueprototype.$store=store 
const store = new Vuex.Store({
  state: {
    icons: ['canying', 'gouwu', 'zufang', 'jiaotong', 'yule', 'yiliao','huafei', 'gongzi','jiangjin'],
    recordList: [],
    createRecordError: null,
    createTagError: null,
    tagList: [],
    currentTag: undefined
  } as RootState,
  mutations: {
    //
    fetchRecords(state) {
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]') as RecordItem[]; //record
    },
    //
    createRecord(state, record: RecordItem) {
      const record2: RecordItem = clone(record);
      record2.createdAt = record2.createdAt || new Date().toISOString();
      // this.recordList && this.recordList.push(record2);
      state.recordList.push(record2);
      store.commit('saveRecords');
    },
    //
    saveRecords(state) {
      window.localStorage.setItem('recordList', JSON.stringify(state.recordList));
    },
    //
    fetchTags(state) {
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]');
      if (!state.tagList || state.tagList.length === 0) {
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
      }
    }
    ,
    //
    createTag(state, name: string) {
      //this.data=[{id:'1',name:'1'},{id:'2',name:'2'}] maptag[]name
      state.createTagError = null;  //
      const names = state.tagList.map(item => item.name);
      if (names.indexOf(name) >= 0) {
        // window.alert('');
        state.createTagError = new Error('tag name duplicated');
        return;
      } else {
        const id = createId().toString();
        const icon = state.icons[parseFloat(id) - 1] || 'zhichu';
        state.tagList.push({id: id, name: name, icon: icon});
        store.commit('saveTags');
      }
    },
    //
    saveTags(state) {
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
    },
    // 
    setCurrentTag(state, id: string) {
      state.currentTag = state.tagList.filter(t => t.id === id)[0];//findfilter
    },
    //
    removeTag(state, id: string) {
      let index = -1;
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i;
          break;
        }
      }
      if (index >= 0) {
        state.tagList.splice(index, 1);//
        store.commit('saveTags');//
        router.back();
      } else {
        window.alert('');
      }
    },
    //
    updateTag(state, payload: { id: string; name: string }) {
      const {id, name} = payload;
      const idList = state.tagList.map(item => item.id); //id
      if (idList.indexOf(id) >= 0) {
        const names = state.tagList.map(item => item.name);
        if (names.indexOf(name) >= 0) {//
          window.alert('');
        } else {
          const tag = state.tagList.filter(item => item.id === id)[0];
          tag.name = name;
          store.commit('saveTags');
        }
      }
    },
  }
});
export default store;
