










import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tags from '@/components/Money/Tags.vue';
import FormItem from '@/components/Money/Notes.vue';
import NumberPad from '@/components/Money/NumberPad.vue';
import recordTypeList from '@/constant/recordTypeList';
import Tabs from '@/components/Tabs.vue';

window.localStorage.version = '0.0.1';

@Component({
  components: {Tabs, NumberPad, FormItem, Tags},
  //
  created() {
    this.$store.commit('fetchRecords');
  }
}) //componentsdata
export default class Money extends Vue {

  record: RecordItem = {tags: [], notes: '', type: '-', amount: 0, createdAt: new Date().toISOString()};
  recordTypeList = recordTypeList;
  h = document.body.clientHeight;

  get recordList() {
    return this.$store.state.recordList;
  }


  saveRecord() {
    if (!this.record.tags || this.record.tags.length === 0) {
      return window.alert('');
    }
    this.$store.commit('createRecord', this.record);
    if (this.$store.state.createRecordError === null) {
      this.$router.replace('/statistics')
      // window.alert('');
      this.record.notes = ''; //
    }
    this.$store.commit('fetchTags');
  }

  // push
  // const record2: RecordItem = JSON.parse(JSON.stringify(this.record));
  //localStorage.set('recordList',JSON.stringify(this.recordList)) //recordListlocalStorageJSON.stringify


  // @Watch('recordList') watch
  // window.localStorage.setItem('recordList',JSON.stringify(this.recordList))
  // onRecordListChange() {
  //   recordListModel.saveRecords();
  // }
}
